import React from 'react';
import './assets/css/fonts.css';
import { Switch, Route, useLocation, useHistory } from 'react-router-dom';

import { MuiThemeProvider } from '@material-ui/core/styles';

import {
  CssBaseline,
  Container,
  Box,
} from '@material-ui/core';

import { theme } from './utils/theme';

import { STYLES } from './utils/constants';

import { Home } from './Home';
import { Footer } from './Base/Footer';
import { Profile } from './Profile';
import { LearningCenter } from './LearningCenter';
import { CompanyPage } from './CompanyPage';
import { Premium } from './Premium';
import { RatingPage } from './RatingPage';

function App() {
  const userType = '';

  const urlRoot = window.location.hostname;
  const location = useLocation();
  const history = useHistory();
  let urlPrefix = location.pathname;

  const redirectProd = () => history.push('/autismo-tech/' + urlPrefix);
  const redirectLocal = () => history.push('/');

  console.log(urlRoot.includes("localhost") && urlPrefix.includes("autismo-tech"));
  console.log(history)

  if (urlRoot.includes("github.io") && !urlPrefix.includes("autismo-tech")) {
    console.log('aaa');
    redirectProd();
  }

  if (urlRoot.includes("localhost") && urlPrefix.includes("autismo-tech")) {
    redirectLocal();
  }

  return (
    <div className="App">
      <MuiThemeProvider theme={theme}>
        <CssBaseline />
        <Switch>
          <Route exact path="/" component={() => <Home userType={userType} />} />
          <Route path="/profile" component={Profile} />
          <Route path="/company/:companyId" component={CompanyPage} />
          <Route path="/learning-center" component={LearningCenter} />
          <Route path="/premium" component={Premium} />
          <Route path="/rate-company" component={RatingPage} />
        </Switch>
        <Box style={STYLES.FOOTER.BACKGROUND} mt={8} p={4}>
          <Container maxWidth="lg">
            <Footer userType={userType} />
          </Container>
        </Box>
      </MuiThemeProvider>
    </div>
  );
}

export default App;
